import { render } from "react-dom";
import {
  Link,
  Routes,
  Route,
  NavLink
} from "react-router-dom";
import SingleCart from './SingleCart/SingleCart';
import Loading from '../../../loading/loading';
import Skeleton from '../../../Skeleton/Skeleton';
import WeekCart from '../../Search/Result/WeekCart/WeekCart';

import styles from './Result.module.scss';


const Result = ({weather, weatherMounth}) => {

    return(
        <div className={styles.ResultWrap}>
            <div className={styles.Navigation}>
            <NavLink to="/" className={({ isActive }) =>(isActive ? styles.active : "")} >Forecast for one day</NavLink>
            <NavLink to="/week" className={({ isActive }) =>(isActive ? styles.active : "")} >Forecast for 40 days</NavLink>
            
            </div>
            
            <Routes>
                <Route path="/" element={weather.length !== 0 ?  <SingleCart giveWeather={weather}/> : <Loading /> }/>
                <Route path="/week" element={<WeekCart weatherMounth={weatherMounth}/>}/>
                <Route path="*" element={<Skeleton><p>404</p></Skeleton>}/>
            </Routes>
           
        </div>
    )
}

export default Result;