
const useApiService = () =>{

const _apiBase = "https://api.openweathermap.org/data/2.5/";
const _apiKey = "b2e5e911966f3b1bb4269dd253d77ae7";




const getWeatherMunth = async (city="london") => {
     return await fetch(`${_apiBase}forecast?q=${city}&lang=ru&units=metric&appid=${_apiKey}`)
            .then(result => result.json())
}  


const getWeather = async (city="london") => {
     return await fetch(`${_apiBase}weather?q=${city}&units=metric&appid=${_apiKey}`)
            .then(result => result.json())
            
}    




return {getWeather , getWeatherMunth};      
}

export default useApiService;    