import { useEffect, useState } from 'react';
import useApiService from '../../services/services';
import Loading from '../loading/loading';

import Search  from './Search/Search';
import Result from './Search/Result/Result';

import styles from './Main.module.scss';
import SkeletonWrap from '../Skeleton/Skeleton';


const Main = () => {

  const [isLoading, setIsLoading] = useState(true);
  const [weather, setWeather] = useState([]);
  const [weatherMounth, setWeatherMounth] = useState(null);
  const [city, setCity] = useState("");
  const [search, setSearch] = useState(0);
  const [errorWeather, setErrorWeather] = useState(false);

  const changeValueCity = (even) => {
     even.preventDefault();
     const value = even.target.value;
      setCity(value)
  };

  const submitWeather = (even) =>{
    even.preventDefault();
    if(city.length > 0){
      setSearch((search)=> search + 1)
    }
    
  };

  const errorWeatherToggle = (error) => {
    if(search > 0){
      setErrorWeather(true)
    }
  }

  let {getWeather, getWeatherMunth} = useApiService();

  const getWeatherData = (weather) => {
    if(weather.cod != 200){
      errorWeatherToggle(weather.cod);
    }else{
       setWeather(weather);
       setErrorWeather(false);
    }
 }  
 const getWeatherMounthData = (weather) => {
  if(weather.cod != 200){
    errorWeatherToggle(weather.cod);
  }else{
     setWeatherMounth(weather);
     setErrorWeather(false);
  }
}   


  useEffect(() => {
    getWeather(city)
      .then(setIsLoading(true))
      .then(getWeatherData)
      .then(setIsLoading(false))   

      getWeatherMunth(city)
      .then(setIsLoading(true))
      .then(getWeatherMounthData)   
      .then(setIsLoading(false))    
  },[search]);     
 






 let content = isLoading ?
  <SkeletonWrap ><p>dowlanding...</p></SkeletonWrap> : 
  <Result weather={weather} weatherMounth={weatherMounth}/>

let isError = errorWeather ? <SkeletonWrap ><p>not found...</p></SkeletonWrap> : content ;
let viewContent = (search > 0) ? isError : null;


  return (
      <div className={styles.wrapper}  data-error={errorWeather ? "error" : null}>
        <Search
          changeValueCity={changeValueCity} 
          city={city} submitWeather={submitWeather}/>
          {viewContent}
      </div>
  )
}

export default Main;