import OneDay from '../../../../OneDay/OneDay';

const SingleCart = ({giveWeather}) => {
    
    let weather = giveWeather;
    let name = weather.name;
    let temp = weather.main.temp;
    let feels_like = weather.main.feels_like;
    let clouds = weather.clouds.all;
    let weatherImg = weather.weather[0].main;


    return(
       <OneDay name={name} temp={temp} weatherImg={weatherImg} clouds={clouds} feels_like={feels_like}/>
    )
}

export default SingleCart;