import { Pagination } from "swiper";
import { Swiper, SwiperSlide } from 'swiper/react';
import OneDay from '../../../../OneDay/OneDay';

import "swiper/css/pagination";
import 'swiper/css';
import styles from './WeekCart.module.scss';


const WeekCart = ({weatherMounth}) => {
  
  let content = weatherMounth.list.map((day, i)=> {
  return(
    <SwiperSlide key={i}>
      <OneDay name={weatherMounth.city.name} 
      temp={day.main.temp}
      weatherImg={day.weather[0].main}
      clouds={day.clouds.all}
      feels_like={day.main.feels_like}
      />
   </SwiperSlide> 
  
  )
  
  
  })
   

  



    return(
        <Swiper className={styles.wrap}
        pagination={{
          dynamicBullets: true,
        }}
        modules={[Pagination]}
        spaceBetween={50}
        breakpoints={{
          640: {
            width: 640,
            slidesPerView: 1,
          },
          1250: {
            slidesPerView: 2,
          },
          1550: {
            slidesPerView: 3,
          },
        }}
      >
        {content}
      </Swiper>
    )
}

export default WeekCart;