import cloudyNight from '../../assets/img/clouds.jpg';
import thunder from '../../assets/img/Thunderstorm.png';
import drizzle from '../../assets/img/Drizzle.png';
import rainy from '../../assets/img/Rain.png';
import snow from '../../assets/img/Snow.png';
import clearDay from '../../assets/img/Clear.jpg';

import styles from './OneDay.module.scss';

const OneDay = ({weatherImg, name, temp, feels_like, clouds}) => {
    let img;
    switch (weatherImg){
        case "Clouds": 
            img = cloudyNight;
            break;

        case "Thunderstorm": 
            img = thunder;
            break;

        case "Drizzle": 
            img = drizzle;
            break;

        case "Rain":
            img = rainy;
            break;

        case "Snow": 
            img = snow;
            break;

        case "Clear":
            img = clearDay;
            break;

        default: 
            img = clearDay;
            break;
    }


    return(
        <div className={styles.cartWrap}>        
        <img src={img} alt="" />
          <div className={styles.imgWeather}>
        </div>
        <div className={styles.nameCity}>
            {name}
        </div>
        <div className={styles.cartWrapDecription}>
            <p><span>Temp</span>:<span>{Math.ceil(temp)}</span></p>
            <p><span>Feels like</span>:<span>{Math.ceil(feels_like)} </span></p>
            <p><span>Clouds</span>:<span>{clouds}%</span></p>  
        </div>
    </div> 
    )
}

export default OneDay;