
import styles from './Search.module.scss';

const Search = ({changeValueCity, city, submitWeather}) => {
    
   

    return (
        <>
          <form className={styles.searchForm} action="#">
              <input 
              onChange={(even)=> changeValueCity(even)}
              type="text" placeholder='write city...' value={city}/>
              <button
              onClick={(even)=> submitWeather(even)}
               >Weather</button>
          </form>
        </>
    )
}

export default Search;